const lightTheme = {
    body: '#e2e2e2',
    text: '#363537',
    footer: '#424242',
    toggleBorder: '#fff',
    gradient: 'linear-gradient(#39598A, #79D7ED)',
}

const darkTheme = {
    body: '#363537',
    text: '#FAFAFA',
    footer: '#424242',
    toggleBorder: '#6B8096',
    gradient: 'linear-gradient(#091236, #1E215D)',
}

const theme = mode => (mode === "dark" ? darkTheme : lightTheme);

export default theme;