import React from 'react';

function AppMenuBar(props) {
    const { menus } = props;

    return (
        <React.Fragment>
            <nav className="row app2">
                {menus.map((menu) => (
                    <AppMenu key={menu.title} menu={menu} />
                ))}
            </nav>
        </React.Fragment>
    );
}

function AppMenu(props) {
    return (
        <a
            className="menu1 App-underlineHover App-colorInherit App-noWrap  Menu-Body col-2"
            href={props.menu.url}>
            {props.menu.title}
        </a>
    );
}

export default AppMenuBar;