import React from 'react';
import AppMenuBar from './AppMenuBar';

const menus = [
    { title: 'Java', url: '#' },
    { title: 'Angular', url: '#' },
    { title: 'React', url: '#' },
    { title: 'Spring', url: '#' },
];

function AppHeader() {
    return (
        <div className="container">
            <div className="row AppToolbar-root AppToolbar-regular AppToolbar-gutters app1">
                <div className="col-2">
                    <AppLogo />
                </div>
                <div className="col-8 d-flex align-items-center justify-content-center">
                    <AppTitle />
                </div>
                <div className="col-2">
                    <AppThemeChanger />
                </div>
            </div>
            <div className="row AppToolbar-root AppToolbar-dense AppToolbar-gutters app1">
                <AppMenuBar menus={menus} />
            </div>
        </div>
    );
}

function AppLogo() {
    return (
        <div>
            <img
                src={process.env.PUBLIC_URL + '/logo512.png'}
                width="50"
                height="50"
                alt="TV"
            />
        </div>
    );
}

function AppTitle() {
    return (
        <div>
            <h2>Tech Vigorous</h2>
        </div>
    );
}

function AppThemeChanger() {
    return (
        <div>

        </div>
    );
}

export default AppHeader;